* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  letter-spacing: 1.5px;
}
body,
main,
#root {
  width: 100vw;
  height: auto;
  background-color: rgb(247, 247, 247);
  overflow-x: hidden;
}
.container {
  position: absolute;
  left: 50%;
  top: 215px;
  transform: translateX(-50%);
  width: 840px;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: inset 0px 0px 10px rgb(168, 168, 168);
  border-radius: 5px;
  margin-bottom: 1rem;
  z-index: 0;
}
.alert-container {
  height: 50px;
}
header {
  z-index: 1;
  background-color: rgb(247, 247, 247);
  position: fixed;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header h1 {
  padding-left: 5rem;
  font-size: 3rem;
  color: rgb(88, 88, 88);
}
header article {
  padding-right: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 400px;
}
header article select {
  padding: 3px 10px;
  border: none;
  background: transparent;
  box-shadow: 2px 2px 5px grey;
  border-radius: 10px;
  outline: none;
  width: 140px;
}
header article select option {
  background-color: rgb(247, 247, 247);
}

.danger {
  color: red;
  background-color: rgb(255, 248, 248);
  padding: 5px 25px;
  font-weight: 500;
}
.success {
  color: green;
  font-weight: 500;
  background-color: rgb(199, 255, 199);
  padding: 5px 25px;
}
.form-container {
  width: 100%;
}
.div-form form {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
input[type="text"] {
  width: 400px;
  height: 40px;
  padding-left: 1rem;
  font-size: 1rem;
  outline: none;
  border-radius: 10px;
  border: none;
  background: transparent;
  box-shadow: 2px 2px 5px grey;
}
input[type="text"]:focus {
  box-shadow: inset 3px 3px 10px rgb(179, 179, 179);
}
.radio-container {
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  padding: 0 1rem;
  box-shadow: inset 3px 3px 10px rgb(179, 179, 179);
  border-radius: 10px;
}
.high-radio input {
  margin-left: 0.5rem;
}
.low-radio input {
  margin-left: 0.5rem;
}
form button {
  height: 40px;
  width: 40px;
  border: none;
  outline: none;
  box-shadow: 2px 2px 5px grey;
  border-radius: 10px;
}
form button:hover {
  color: rgb(0, 68, 255);
}
form button:active {
  box-shadow: inset 0px 0px 4px grey;
}
.items-container {
  width: 100%;
}
.item-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 2rem 0;
}
.name-container {
  width: 50%;
  box-shadow: inset 3px 3px 10px rgb(179, 179, 179);
  padding: 0.5rem 1rem;
  border-radius: 10px;
  word-break: break-all;
  /*To handle text overflow*/
}

.btn-container {
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn-container > button:nth-child(1) {
  box-shadow: 2px 2px 5px grey;
  color: rgb(97, 97, 97);
  font-size: 0.9rem;
  padding: 8px;
  border: none;
  outline: none;
  border-radius: 0.5rem;
}
.btn-container > button:nth-child(1):hover {
  color: rgb(6, 255, 6);
}
.btn-container > button:nth-child(1):active {
  box-shadow: inset 0px 0px 5px grey;
}
.btn-container > button:nth-child(2) {
  box-shadow: 2px 2px 5px grey;
  color: rgb(97, 97, 97);
  border: none;
  outline: none;
  font-size: 0.9rem;
  padding: 8px;
  border-radius: 0.5rem;
}
.btn-container > button:nth-child(2):hover {
  color: rgb(245, 212, 26);
}
.btn-container > button:nth-child(2):active {
  box-shadow: inset 0px 0px 5px grey;
}
.btn-container > button:nth-child(3) {
  box-shadow: 2px 2px 5px grey;
  color: rgb(97, 97, 97);
  border: none;
  outline: none;
  font-size: 0.9rem;
  padding: 8px;
  border-radius: 0.5rem;
}
.btn-container > button:nth-child(3):hover {
  color: red;
}
.btn-container > button:nth-child(3):active {
  box-shadow: inset 0px 0px 5px grey;
}
.btn-container p {
  color: gold;
  padding: 10px;
  font-size: 1rem;
}
.btn-container .non-priority {
  opacity: 0.1;
}
.clear {
  width: 60%;
  border: none;
  outline: none;
  padding: 5px 25px;
  color: grey;
  font-weight: 600;
  font-size: 1rem;
  margin-top: 2rem;
  background: transparent;
  border-radius: 10px;
  box-shadow: 2px 2px 5px grey;
}
.clear:hover {
  color: red;
}
.clear:active {
  box-shadow: inset 0px 0px 5px grey;
}
@media screen and (max-width: 820px) {
  header {
    flex-direction: column;
    height: auto;
  }
  header h1 {
    padding-left: 0;
  }
  header article {
    padding-right: 0;
    width: 82%;
    padding: 1rem 0;
  }
  .container {
    width: 95%;
    padding: 1.2rem;
  }
  .div-form form {
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
  }
  form {
    height: 150px;
  }
  input[type="text"] {
    width: 100%;
  }
}
@media screen and (max-width: 550px) {
  header h1 {
    font-size: 2.5rem;
  }
  header article {
    width: 100%;
  }
  .item-container {
    flex-direction: column;
    box-shadow: 1px 1px 3px grey;
    padding: 10px;
  }
  .items-container .name-container {
    margin-bottom: 10px;
  }
  .radio-container {
    flex-direction: column;
    width: 97%;
    height: auto;
    padding: 5px;
    margin: 12px 0;
  }
}
.add {
  background-color: blue;
}
